import React, { FC, useEffect } from 'react';
import { connect } from 'react-redux'
import { Button, Form, Input, message } from 'antd';
import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { LoginWrap } from "./login-style";
import logo from '../../assets/images/logo.png'
import { TOLOGIN } from "./actions";
import { LoginApi } from '../../common/api'
import { Dispatch } from "redux";
import DocumentTitle from 'react-document-title'
import { getStore } from "../../utils/storage";
import { useHistory } from "react-router-dom";

type Props = {
  status: number,
  toLogin(values: object): void,
  clearState(): void
  [prop: string]: any

};
type userInput = {
  account: number | string
  password: string
}

const mapStateToProps = (state: any) => ({
  status: state.login.status
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  toLogin(values: object) {
    dispatch({
      type: TOLOGIN,
      url: LoginApi,
      data: values
    })
  }
})


const Login: FC<Props> = (props) => {

  const history = useHistory()

  useEffect(() => {
    if (String(props.status)) {
      const { status } = props
      console.log(props)
      if (status === -1) message.warning('');
      else if (status > 100) message.error('')
      else {
        if (getStore('userInfo') !== null) {
          const info = JSON.parse(getStore('userInfo'))
          if (info.role === 'admin') history.push('/admin/dashboard')
          else if (info.role === 'chef') history.push('/chef/orders')
          else history.push('/waiter')
          message.success(`${info.name}`)
        }
      }
    }
  }, [props.status])

  /**
   * 
   * @param values 
   */
  // todo 
  function onFinish(values: userInput): void {
    const { account, password } = values;
    if ((account as string).trim() === '' || password.trim() === '') {
      message.error('')
    } else {
      props.toLogin(values)
    }
  }

  return (
      <DocumentTitle title=''>
        <LoginWrap>
          <Form
              name="normal_login"
              className="login-form"
              initialValues={{
                remember: true,
              }}
              onFinish={onFinish}
          >
            <img className='logo-left' src={logo} alt='logo'/>
            <img className='logo' src={logo} alt=''/>
            <span className='login-form-title'></span>
            <Form.Item
                name="account"
                rules={[
                  {
                    required: true,
                    message: '',
                  },
                ]}
            >
              <Input prefix={<UserOutlined className="site-form-item-icon"/>} placeholder=""/>
            </Form.Item>
            <Form.Item
                name="password"
                rules={[
                  {
                    required: true,
                    message: '',
                  },
                ]}
            >
              <Input.Password prefix={<LockOutlined className="site-form-item-icon"/>}
                              type="password"
                              placeholder=""/>
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" size="large"
                      className="login-form-button"></Button>
              <div className="login-form-login"><span><a href="???">...</a></span></div>
            </Form.Item>
          </Form>
        </LoginWrap>
      </DocumentTitle>
  );
};


export default connect(mapStateToProps, mapDispatchToProps)(Login);
