import React, { FunctionComponent, useEffect, useState } from 'react';
import {
  Avatar,
  Button,
  ConfigProvider,
  Descriptions,
  Form,
  Input,
  Layout,
  Menu,
  message,
  Modal,
  notification,
  Popover,
  Space,
  Tag
} from 'antd';
import {
  HeartOutlined,
  InsertRowBelowOutlined,
  LineChartOutlined,
  ShopOutlined,
  TeamOutlined,
  UnorderedListOutlined
} from '@ant-design/icons';
import logo from '../../assets/images/logo_small.png'
import { useHistory, useLocation } from 'react-router-dom';
import './index.css'
// @ts-ignore
import Darkmode from 'darkmode-js';
import moment from 'moment'
import { getStore, removeStore, setStore } from "../../utils/storage";
import zhCN from "antd/es/locale/zh_CN";
import { connect } from "react-redux";
import { Dispatch } from "redux";
import { EDITUSERINFO } from "./actions";
import { PWD_URL } from "../api";
import Time from "../../components/Time";

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

const mapStateToProps = (state: any) => ({
  topBarEditState: state.topBarEditInfo.topBarEditState,
  tberr: state.topBarEditInfo.tberr,
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  editInfo(val: any) {
    dispatch({
      type: EDITUSERINFO,
      url: PWD_URL,
      data: val
    })
  }
})

interface Props {
  editInfo(val: any): void,

  [prop: string]: any
}


const LayoutWrap: FunctionComponent<Props> = (props) => {

  const [collapsed, setCollapsed] = useState(false);
  const history = useHistory()
  const { pathname } = useLocation()

  const options = {
    bottom: '30px', // default: '32px'
    right: '32px', // default: '32px'
    left: 'unset', // default: 'unset'
    time: '0.5s', // default: '0.3s'
    mixColor: '#fff', // default: '#fff'
    backgroundColor: '#fff',  // default: '#fff'
    buttonColorDark: '#100f2c',  // default: '#100f2c'
    buttonColorLight: '#fff', // default: '#fff'
    saveInCookies: false, // default: true,
    label: '', // default: ''
    autoMatchOsTheme: true // default: true
  }
  const darkmode = new Darkmode(options);
  darkmode.showWidget();

  //
  const content = (
      <div className='contentss'>
        <p onClick={() => {
          setVisible(true)
        }}></p>
        <p onClick={() => {
          setVisiblePwd(true)
        }}></p>
        <p onClick={() => {
          removeStore('userInfo')
          removeStore('token')
          removeStore('topBarEditState')
          history.push('/')
        }}></p>
      </div>
  );


  //session
  const [personInfo, setPersonInfo] = useState({
    name: undefined,
    account: undefined,
    role: undefined,
    joinTime: undefined,
    status: undefined
  })
  //
  useEffect(() => {
    const userInfo = JSON.parse(getStore('userInfo'))
    console.log(userInfo)
    setPersonInfo(userInfo)
  }, [])

  const [visible, setVisible] = useState(false)
  const [visiblePwd, setVisiblePwd] = useState(false)

  //
  const handleOks = (val: any) => {
    console.log(val)
    if (val.confirmPwd !== val.newPwd) {
      notification['warning']({
        message: '',
        description: ''
      })
      return
    }
    props.editInfo(val)
  }


  //  fix  
  useEffect(() => {
    // console.log(props)
    const { topBarEditState, tberr } = props
    console.log(tberr)
    const estatus = getStore('topBarEditStatus')
    //
    if (estatus == topBarEditState) {
    } else {
      setStore('topBarEditState', topBarEditState)
      if (topBarEditState < 1) {
        message.success('')
        setVisiblePwd(false)
      } else {
        if (tberr === '') message.warn('')
        else if (tberr === '') message.error('')
        else if (tberr === '') message.warn('')
        else if (tberr === '') message.warn('')
      }
      // //
      // setPersonInfo(JSON.parse(getStore('userInfo')))
    }
  }, [props.topBarEditState, props.tberr])

  return (
      <ConfigProvider locale={zhCN}>
        <Layout style={{ minHeight: '100vh' }}>
          <Sider collapsible collapsed={collapsed} onCollapse={() => {
            setCollapsed(!collapsed)
          }}>
            <div className="logo">
              <img style={{ cursor: 'pointer' }} className='bigLogo' src={logo} alt="logo"/>
            </div>

            {/*  admin*/}
            {
              personInfo && personInfo.role === 'admin' &&
              <Menu theme="dark" defaultSelectedKeys={[pathname]} mode="inline">
                <Menu.Item key="/admin/dashboard" icon={<LineChartOutlined/>} onClick={() => {
                  history.push('/admin/dashboard')
                }}>
                  
                </Menu.Item>

                <SubMenu key="sub122" icon={<ShopOutlined/>} title="">
                  <Menu.Item key="/admin/dishes"
                             onClick={() => {
                               history.push('/admin/dishes')
                             }}
                  ></Menu.Item>
                  <Menu.Item key="/admin/dishes/category"
                             onClick={() => {
                               history.push('/admin/dishes/category')
                             }}
                  ></Menu.Item>
                </SubMenu>

                <Menu.Item
                    key="/admin/orders"
                    icon={<UnorderedListOutlined/>}
                    onClick={() => {
                      history.push('/admin/orders')
                    }}
                >
                  
                </Menu.Item>
                <SubMenu key="sub2453" icon={<HeartOutlined/>} title="">
                  <Menu.Item key="/admin/customer"
                             onClick={() => {
                               history.push('/admin/customer')
                             }}
                  ></Menu.Item>
                  <Menu.Item key="/admin/customer/cate"
                             onClick={() => {
                               history.push('/admin/customer/cate')
                             }}
                  ></Menu.Item>
                </SubMenu>
                <Menu.Item
                    key="/admin/staffs"
                    icon={<TeamOutlined/>}
                    onClick={() => {
                      history.push('/admin/staffs')
                    }}
                >
                  
                </Menu.Item>
                <Menu.Item key="/admin/table" icon={<InsertRowBelowOutlined/>} onClick={() => {
                  history.push('/admin/table')
                }}>
                  
                </Menu.Item>
              </Menu>
            }

            {/*  */}
            {
              personInfo && personInfo.role === 'chef' &&
              <Menu theme="dark" defaultSelectedKeys={[pathname]} mode="inline">
                <Menu.Item
                    key="/chef/orders"
                    icon={<UnorderedListOutlined/>}
                    onClick={() => {
                      history.push('/chef/orders')
                    }}
                >
                  
                </Menu.Item>
              </Menu>
            }

            {/*  */}
            {
              personInfo && personInfo.role === 'waiter' &&
              <Menu theme="dark" defaultSelectedKeys={[pathname]} mode="inline">
                <Menu.Item
                    key="/waiter/orders"
                    icon={<UnorderedListOutlined/>}
                    onClick={() => {
                      history.push('/waiter/orders')
                    }}
                >
                  
                </Menu.Item>
                <Menu.Item key="/waiter/table" icon={<InsertRowBelowOutlined/>} onClick={() => {
                  history.push('/waiter/table')
                }}>
                  
                </Menu.Item>
                <SubMenu key="sub2453" icon={<HeartOutlined/>} title="">
                  <Menu.Item key="/waiter/customer"
                             onClick={() => {
                               history.push('/waiter/customer')
                             }}
                  ></Menu.Item>
                  <Menu.Item key="/waiter/customer/cate"
                             onClick={() => {
                               history.push('/waiter/customer/cate')
                             }}
                  ></Menu.Item>
                </SubMenu>
              </Menu>
            }
          </Sider>
          <Layout className="site-layout">
            <Header className="site-layout-background">
              <div></div>
              <div className='time'><Time/></div>
              <div className='avatar'>
                <Popover placement={"leftTop"} content={content} title="">
                  <Avatar size={"large"} style={{ backgroundColor: '#2ed573' }}>{personInfo?.name}</Avatar>
                </Popover>
              </div>
            </Header>
            <Content style={{ margin: '16px 16px' }}>
              {/*<Breadcrumb style={{margin: '16px 0'}}>*/}
              {/*    <Breadcrumb.Item></Breadcrumb.Item>*/}
              {/*    <Breadcrumb.Item>Bill</Breadcrumb.Item>*/}
              {/*</Breadcrumb>*/}
              <div style={{ minHeight: 780, height: '100%' }}>
                {props.children}
              </div>
            </Content>
            <Footer style={{ textAlign: 'center' }}>Copyright  2021  </Footer>
          </Layout>
          <Modal
              visible={visible}
              destroyOnClose
              onCancel={() => {
                setVisible(false)
              }}
              footer={null}
          >
            <Descriptions bordered size={'small'} title="">
              <Descriptions.Item label="">{personInfo?.account}</Descriptions.Item>
              <Descriptions.Item label="">{personInfo?.name}</Descriptions.Item>
              <Descriptions.Item label="">
                {
                  personInfo.role === 'admin' &&
                  <Tag color="#108ee9"></Tag>
                }
                {
                  personInfo.role === 'chef' &&
                  <Tag color="#9c90da"></Tag>
                }
                {
                  personInfo.role === 'waiter' &&
                  <Tag color="#108ee9"></Tag>
                }
              </Descriptions.Item>
              <Descriptions.Item label="">
                {
                  personInfo?.status === 1 && ''
                }
                {
                  personInfo?.status === 0 && ''
                }
              </Descriptions.Item>
              <Descriptions.Item
                  label="">{moment(personInfo?.joinTime).format('YYYY-MM-DD HH:mm:ss')}</Descriptions.Item>
            </Descriptions>
          </Modal>

          <Modal
              visible={visiblePwd}
              destroyOnClose
              onCancel={() => {
                setVisiblePwd(false)
              }}
              title={''}
              footer={null}
          >
            <Form onFinish={handleOks}>
              <Form.Item name="lastPwd" label="" rules={[
                {
                  required: true,
                  message: '',
                },
              ]}>
                <Input.Password type={"password"}/>
              </Form.Item>
              <Form.Item name="newPwd" label="" rules={[
                {
                  required: true,
                  message: '',
                },
              ]}>
                <Input.Password type={"password"}/>
              </Form.Item>
              <Form.Item name='confirmPwd' label="" rules={[
                {
                  required: true,
                  message: '',
                },
              ]}>
                <Input.Password type={"password"}/>
              </Form.Item>
              <Form.Item style={{ marginBottom: -6 }}>
                <Space style={{ float: "right" }}>
                  <Button type="default" onClick={() => {
                    setVisiblePwd(false)
                  }}></Button>
                  <Button type="primary" htmlType="submit"></Button>
                </Space>
              </Form.Item>
            </Form>
          </Modal>
        </Layout>
      </ConfigProvider>
  );
};
export default connect(mapStateToProps, mapDispatchToProps)(LayoutWrap);
