import React, { FunctionComponent, useEffect, useMemo, useRef, useState } from 'react';
import { Button, Card, ConfigProvider, Form, Input, message, Modal, Radio, Space, Table, Tag } from 'antd';
import { ColumnsType } from 'antd/es/table';
import { Dispatch } from "redux";
import { ADDSTAFF, DELSTAFF, EDITSTAFF, TOGGLEPAGE } from "./actions";
import { DELSTAFF_URL, EDITSTAFF_URL, STAFFADD, STAFFLIST } from "../../../common/api";
import DocumentTitle from "react-document-title";
import moment from 'moment';
import { FieldNumberOutlined, LockOutlined, UserOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { getStore, setStore } from "../../../utils/storage";
import zhCN from 'antd/es/locale/zh_CN';
import Paging from "../../../components/Paging";
import TableCheckBox from "../../../components/TableCheckBox";

interface OwnProps {
  [prop: string]: any

  togglePage(value?: object): void

  delStaff(value: string): void,

  addStaff(value: object): void,

  editStaff(value: object): void

}

interface User {
  _id: any,
  name: string,
  role: string,
  title: string,
  status: number,
  account: number,
  joinTime: string
}

type Props = OwnProps;

const mapStateToProps = (state: any) => ({
  list: state.staff.list || [],
  addStatus: state.staff.addStatus,
  delStatus: state.staff.delStatus,
  errorMsg: state.staff.errorMsg,
  editStatus: state.staff.editStatus
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  delStaff(value: string) {
    dispatch({
      type: DELSTAFF,
      url: DELSTAFF_URL,
      data: value
    })
  },
  addStaff(value: object) {
    dispatch({
      type: ADDSTAFF,
      url: STAFFADD,
      data: value
    })
  },
  togglePage(value: object) {
    dispatch({
      type: TOGGLEPAGE,
      url: STAFFLIST,
      data: value
    })
  },
  editStaff(value: object) {
    dispatch({
      type: EDITSTAFF,
      url: EDITSTAFF_URL,
      data: value
    })
  }
})


const Staff: FunctionComponent<Props> = (props) => {

  // 
  const columns: ColumnsType<User> = [
    {
      title: '',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '',
      key: 'role',
      dataIndex: 'role',
      render: (role: any) => (
          <>
            {
              role === 'admin' &&
              <Tag color={'#0083B0'} key={role}>
                {
                  role === 'admin' ? '' : ''
                }
              </Tag>
            }
            {
              role === 'waiter' &&
              <Tag color={'#2ed573'} key={role}>
                {
                  role = ''
                }
              </Tag>
            }
            {
              role === 'chef' &&
              <Tag color={'#a17fe0'} key={role}>
                {
                  role = ''
                }
              </Tag>
            }
          </>
      ),
    },
    {
      title: '',
      key: 'joinTime',
      dataIndex: 'joinTime',
      render: (time: any) => (
          <>
            {
              moment(time).format('YYYY-MM-DD')
            }
          </>
      ),
    },
    {
      title: '',
      key: 'status',
      dataIndex: 'status',
      render: (status: any) => (
          <>
            {
              status === 1 && ''
            }
            {
              status === 0 && ''
            }
            {
              status === -1 && ''
            }
          </>
      ),
    },
    /*{
      title: '',
      key: '_id',
      width: 300,
      render: (text, records, index) => (
          <Space>
            <Button type='primary' size={"small"} onClick={() => {
              edit_staff(records)
            }}></Button>
            {
              <Popconfirm
                  title=""
                  placement="top"
                  okText=""
                  cancelText=""
                  onConfirm={() => {
                    del_staff(records)
                  }}
              >
                <Button type='primary' danger size={"small"}></Button>
              </Popconfirm>
            }
          </Space>
      ),
    },*/
  ];

  //()
  const positionOptions = [
    { label: '', value: 'waiter', style: { marginRight: 10 } },
    { label: '', value: 'chef', style: { marginRight: 10 } },
    { label: '', value: 'admin' },
  ];

  //
  const [pageMsg, setPageMsg] = useState({
    query: '',
    page: 1,
    pagesize: 10
  })
  const [userList, setUserList] = useState([])


  /**
   * 
   * visible: 
   * selected: 
   * popStaffStyle: 
   */
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(['waiter'])
  const [popStaffStyle, setPopStaffStyle] = useState('')
  const [editValues, setEditValues] = useState({ account: 0, name: '', role: '', status: -9 })

  //  
  const handleOk = (value: any) => {
    if (value.hasOwnProperty('password')) {
      //  account
      value.account = props.list.records.reduce((pre: any, cur: any) => pre > cur.account ? pre : `${cur.account + 1}`, 0)
      value.joinTime = new Date().getTime()
      props.addStaff(value)
      setVisible(false);
    } else {
      // @ts-ignore
      value.name = edited_name.current.state.value
      value.account = editValues.account

      // Dispach
      props.editStaff(value)
      setVisible(false)
    }
  };

  const edited_name = useRef(null);

  // 
  const handleCancel = () => {
    console.log('Clicked cancel button');
    setVisible(false);
  };

  // 
  const onChange1 = (e: any) => {
    const selectedValue = e.target.value
    setSelected(selectedValue)
  }

  // 
  const del_staff = (value: User) => {
    const { _id } = value
    props.delStaff(_id)
  }

  // 
  const edit_staff = (value: any) => {
    setVisible(true)
    setPopStaffStyle('')
    setEditValues({
          account: value.account,
          name: value.name,
          role: value.role,
          status: value.status
        }
    )
  }

  //  
  useEffect(() => {
    const { role } = editValues
    setSelected([`${role}`])
    // console.log(selected)
    // console.log(editValues)
  }, [editValues])

  // 
  useEffect(() => {
    props.togglePage(pageMsg)
    const { list } = props
    const user_List = list.records
    setUserList(user_List)
    //  
  }, [props.list.total, props.list.page, props.list.size])

  //   memolist 
  useMemo(() => {
    const { list } = props
    const user_List = list.records
    setUserList(user_List)
  }, [props.list])


  //  fix  
  useEffect(() => {
    const { errorMsg, addStatus, delStatus, editStatus } = props
    const astatus = getStore('addStatus')
    const dstatus = getStore('delStatus')
    const estatus = getStore('editStatus')
    //
    if (astatus == addStatus) {
    } else {
      setStore('addStatus', addStatus)
      if (addStatus < 1) message.success('')
      else if (errorMsg && errorMsg.includes('')) message.warning('3')
      else if (errorMsg && errorMsg.includes('')) message.warning('')
      else if (errorMsg && errorMsg.includes('')) message.warn('')
    }
    //
    if (dstatus == delStatus) {
    } else {
      setStore('delStatus', delStatus)
      if (delStatus < 1) message.success('')
    }
    //
    if (estatus == editStatus) {
    } else {
      setStore('editStatus', editStatus)
      if (editStatus < 1) message.success('')
      //
      SetSelectValue([])
      // @ts-ignore
      ref.current = []
    }
  }, [props.addStatus, props.delStatus, props.editStatus])

  const [editBtnState, setEditBtnState] = useState(false)
  //
  const [selectValue, SetSelectValue] = useState([])
  const [barVisible, setBarVisible] = useState('')
  const ref = useRef();
  const rowSelection = {
    onChange: (selectedRowKeys: any, selectedRows: any) => {
      SetSelectValue(selectedRows)
      ref.current = selectedRowKeys;
      if (selectedRows.length > 1) setEditBtnState(true)
      else setEditBtnState(false)
      console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
    },
    selectedRowKeys: ref.current,
  };
  //
  useEffect(() => {
    SetSelectValue(selectValue)
    if (selectValue.length === 0) setBarVisible('none')
    else setBarVisible('')
  }, [selectValue])
  //pageMsg
  useEffect(() => {
    setPageMsg(pageMsg)
    props.togglePage(pageMsg)
  }, [pageMsg])
  // 
  useEffect(() => {
    const { list } = props
    const order_List = list.records
    setUserList(order_List)
  }, [props.list.records])

  //
  function delSelected() {
    // @ts-ignore
    const delAry = selectValue.map(val => val._id)
    //  
    const delIds = delAry.join('-')
    props.delStaff(delIds)
    setBarVisible('none')
  }

  // @ts-ignore
  return (
      <DocumentTitle title="">
        <ConfigProvider locale={zhCN}>
          <Card title=""
                extra={
                  <Button type="primary" onClick={() => {
                    setVisible(true)
                    setPopStaffStyle('')
                  }} style={{ borderRadius: 5 }}></Button>
                }
                headStyle={{}}
                style={{ width: '100%' }}
          >
            {/*<Button style={{ display: barVisible, float: "left", marginRight: 10 }}*/}
            {/*        onClick={() => {*/}
            {/*          console.log(selectValue)*/}
            {/*          edit_staff(selectValue[0])*/}
            {/*        }}*/}
            {/*>*/}
            {/*  */}
            {/*</Button>*/}
            <TableCheckBox
                delSelected={delSelected}
                barVisible={barVisible}
                editBtn={() => {
                  edit_staff(selectValue[0])
                }}
                editBtnState={editBtnState}
                Search={(value: string) => {
                  setPageMsg({
                    page: 1,
                    pagesize: 10,
                    query: value
                  })
                }}
                title=''
            />

            {
              userList &&
              < Table
                  rowKey='_id'
                  bordered
                  columns={columns}
                  dataSource={userList}
                  pagination={false}
                  rowSelection={{ ...rowSelection }}
                  scroll={{ y: 400 }}
              />
            }
            {
              userList &&
              <Paging page={props.list.page} total={props.list.total} fun={(page = 1, pageSize = 10): any => {
                props.togglePage(pageMsg)
                setPageMsg({
                  query: pageMsg.query,
                  page: page,
                  pagesize: pageSize
                })
              }}/>
            }
            {/**/}
            <Modal
                title={popStaffStyle}
                visible={visible}
                onCancel={handleCancel}
                destroyOnClose
                footer={null}
            >
              <Form name="normal_login" className="login-form" onFinish={handleOk}>
                <Form.Item name="account">
                  {
                    popStaffStyle === '' &&
                    <Form.Item name="account" style={{ margin: 0 }}
                               initialValue={props.list.records.reduce((pre: any, cur: any) => {
                                 // console.log(pre, cur.account)
                                 return pre > cur.account ? `${pre}` : `${cur.account + 1}`
                               }, 0)}>
                      <Input
                          disabled prefix={<FieldNumberOutlined className="site-form-item-icon"/>}
                          placeholder=""/>
                    </Form.Item>
                  }
                  {
                    popStaffStyle === '' &&
                    <Form.Item name="account" style={{ margin: 0 }}>
                      <Input
                          defaultValue={editValues.account}
                          disabled prefix={<FieldNumberOutlined className="site-form-item-icon"/>}
                          placeholder=""/>
                    </Form.Item>
                  }
                </Form.Item>
                {
                  popStaffStyle === '' &&
                  <Form.Item name="name" rules={[
                    {
                      required: true,
                      message: '',
                    },
                  ]}>
                    <Input autoFocus={true}
                           prefix={<UserOutlined className="site-form-item-icon"/>}
                           placeholder="2"/>
                  </Form.Item>
                }
                {
                  popStaffStyle === '' &&
                  <Form.Item name="name" initialValue={editValues.name}>
                    <Input autoFocus={true}
                           defaultValue={editValues.name}
                           ref={edited_name}
                           prefix={<UserOutlined className="site-form-item-icon"/>}
                           placeholder="2"/>
                  </Form.Item>
                }
                {
                  popStaffStyle === '' &&
                  <Form.Item name="password" rules={[
                    {
                      required: true,
                      message: '',
                    },
                  ]}>
                    <Input.Password prefix={<LockOutlined className="site-form-item-icon"/>}
                                    type="password"
                                    placeholder="3"/>
                  </Form.Item>
                }
                <Form.Item name="role" initialValue={popStaffStyle === '' ? selected[0] : 'waiter'}>
                  <Radio.Group
                      options={positionOptions}
                      onChange={onChange1}
                      value={selected[0]}
                      optionType="button"
                  />
                </Form.Item>
                <Form.Item name="status" initialValue={popStaffStyle === '' ? editValues.status : 1}
                           style={{ marginTop: -10 }}>
                  <Radio.Group>
                    <Radio value={1}></Radio>
                    <Radio value={0}></Radio>
                    <Radio value={-1}></Radio>
                  </Radio.Group>
                </Form.Item>
                <Form.Item style={{ marginBottom: -6 }}>
                  <Space style={{ float: "right" }}>
                    <Button type="default" onClick={handleCancel}></Button>
                    {
                      popStaffStyle === '' &&
                      <Button type="primary" htmlType="submit"
                              className="login-form-button"></Button>
                    }
                    {
                      popStaffStyle === '' &&
                      <Button type="primary" htmlType="submit"
                              className="login-form-button"></Button>
                    }
                  </Space>
                </Form.Item>
              </Form>
            </Modal>
          </Card>
        </ConfigProvider>
      </DocumentTitle>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(Staff);
