import React, { FunctionComponent, useEffect, useMemo, useState } from 'react';
import { Button, Card, ConfigProvider, Form, Input, message, Modal, Select, Space, Table, } from "antd";
import zhCN from "antd/es/locale/zh_CN";
import Paging from "../../../../components/Paging";
import DocumentTitle from "react-document-title";
import { ColumnsType } from "antd/es/table";
import { Dispatch } from "redux";
import { CUSADD_URL, CUSDEL_URL, CUSEDIT_URL, CUSLIST_URL, } from "../../../../common/api";
import { connect } from "react-redux";
import { CUSADD, CUSDEL, CUSEDIT, GETCUSLIST } from "./action";
import { getStore, setStore } from "../../../../utils/storage";
import TableCheckBox from "../../../../components/TableCheckBox";

const { Option } = Select;

interface OwnProps {
  [prop: string]: any

  toggleCusPage(value?: object): void

  delCus(value: string): void,

  addCus(value: object): void,

  editCus(value: object): void
}

type Props = OwnProps;

interface Cus {
  _id: string,
  name: string,
  telephone: number,
  xftimes: number,
  xftotal: number,
  status: number,
  level: {
    name: string,
    discount: number,
    _id: string
  }
}

const mapStateToProps = (state: any) => ({
  list: state.cus.list || [],
  cateList: state.cus.cateList,
  addCStatus: state.cus.addCStatus,
  delCStatus: state.cus.delCStatus,
  errorMsgC: state.cus.errorMsgC,
  editCStatus: state.cus.editCStatus
})
const mapDispatchToProps = (dispatch: Dispatch) => ({
  delCus(value: string) {
    dispatch({
      type: CUSDEL,
      url: CUSDEL_URL,
      data: value
    })
  },
  addCus(value: object) {
    dispatch({
      type: CUSADD,
      url: CUSADD_URL,
      data: value
    })
  },
  toggleCusPage(value: object) {
    dispatch({
      type: GETCUSLIST,
      url: CUSLIST_URL,
      data: value
    })
  },
  editCus(value: object) {
    dispatch({
      type: CUSEDIT,
      url: CUSEDIT_URL,
      data: value
    })
  }
})

const CusList: FunctionComponent<Props> = (props) => {

      const [cusList, setCusList] = useState([])
      const [visible, setVisible] = useState(false);
      const [popCusStyle, setPopCusStyle] = useState('')
      const [editValue, setEditValue] = useState({
        _id: '',
        name: '',
        level: '',
        status: 0,
        telephone: 0
      })

      // 
      const columns: ColumnsType<Cus> = [
        {
          title: '',
          dataIndex: 'name',
          key: 'name',
        },
        {
          title: '',
          key: 'level',
          dataIndex: 'level',
          render: (level: any) => <>{level?.name}</>,
        },
        {
          title: '',
          key: 'level',
          dataIndex: 'level',
          render: (level: any) => <>{level?.discount}</>,
        },
        {
          title: '',
          key: 'xftimes',
          dataIndex: 'xftimes',
        },
        {
          title: '',
          key: 'xftotal',
          dataIndex: 'xftotal',
        },
        {
          title: '',
          key: 'telephone',
          dataIndex: 'telephone',
        },
        {
          title: '',
          key: 'status',
          dataIndex: 'status',
          render: (status: any) => (
              <>
                {
                  status === 1 && ''
                }
                {
                  status === 0 && ''
                }
                {
                  status === -1 && ''
                }
              </>
          ),
        },
      ];

      // 
      const handleCancel = () => {
        console.log('Clicked cancel button');
        setVisible(false);
      };

      // 
      const edit_cus = (records: Cus) => {
        console.log(records)
        setVisible(true)
        setPopCusStyle('')
        setEditValue({
          _id: records._id,
          name: records.name,
          level: records.level._id,
          telephone: records.telephone,
          status: records.status
        })
      }

      // 
      const del_cus = (value: Cus) => {
        const { _id } = value
        props.delCus(_id)
      }


      //  
      const handleOk = (value: any) => {
        if (popCusStyle === '') {
          props.addCus(value)
          setVisible(false)
        } else {
          value._id = editValue._id
          console.log(value)
          props.editCus(value)
          setVisible(false)
        }
      }
      //
      const [pageMsg, setPageMsg] = useState({
        query: '',
        page: 1,
        pagesize: 10
      })
      useEffect(() => {
        props.toggleCusPage(pageMsg)
        const { list } = props
        const cus_List = list.records
        setCusList(cus_List)
        //  
      }, [props.list.total, props.list.page, props.list.size])

      //   memolist 
      useMemo(() => {
        const { list } = props
        const cusList = list.records
        setCusList(cusList)
      }, [props.list])

      //  fix  
      useEffect(() => {
            const { errorMsgC, addCStatus, delCStatus, editCStatus } = props
            const astatus = getStore('addCStatus')
            const dstatus = getStore('delCStatus')
            const estatus = getStore('editCStatus')
            //
            if (astatus == addCStatus) {
            } else {
              setStore('addCStatus', addCStatus)
              if (addCStatus < 1) message.success('')
              else if (errorMsgC && errorMsgC.includes('')) message.warning('')
            }
            //
            if (dstatus == delCStatus) {
            } else {
              setStore('delCStatus', delCStatus)
              if (delCStatus < 1) message.success('')
            }
            //
            if (estatus == editCStatus) {
            } else {
              setStore('editCStatus', editCStatus)
              if (editCStatus < 1) message.success('')
            }
          }, [props.addCStatus, props.delCStatus, props.editCStatus]
      )

//
      const [selectValue, SetSelectValue] = useState([])
      const [barVisible, setBarVisible] = useState('')
      const rowSelection = {
        onChange: (selectedRowKeys: any, selectedRows: any) => {
          SetSelectValue(selectedRows)
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        onSelectAll: (selected: any, selectedRows: any, changeRows: any) => {
          console.log(selected, selectedRows, changeRows);
        },
      };
      //
      useEffect(() => {
        SetSelectValue(selectValue)
        if (selectValue.length === 0) setBarVisible('none')
        else setBarVisible('')
      }, [selectValue])
      //pageMsg
      useEffect(() => {
        setPageMsg(pageMsg)
        props.toggleCusPage(pageMsg)
      }, [pageMsg])
      // 
      useEffect(() => {
        const { list } = props
        const order_List = list.records
        setCusList(order_List)
      }, [props.list.records])

      //
      function delSelected() {
        // @ts-ignore
        const delAry = selectValue.map(val => val._id)
        //  
        const delIds = delAry.join('-')
        props.delCus(delIds)
        setBarVisible('none')
      }

      return (
          <DocumentTitle title=" > ">
            <ConfigProvider locale={zhCN}>
              <Card title=""
                    extra={
                      <Button type="primary" onClick={() => {
                        setVisible(true)
                        setPopCusStyle('')
                      }} style={{ borderRadius: 5 }}></Button>
                    }
                    style={{ width: '100%' }}
              >
                <TableCheckBox
                    delSelected={delSelected}
                    barVisible={barVisible}
                    editBtn={() => {
                      edit_cus(selectValue[0])
                    }}
                    Search={(value: string) => {
                      setPageMsg({
                        page: 1,
                        pagesize: 10,
                        query: value
                      })
                    }}
                    title=''
                />

                {
                  cusList &&
                  <Table
                      rowKey='_id'
                      bordered
                      columns={columns}
                      dataSource={cusList}
                      pagination={false}
                      rowSelection={rowSelection}
                  />
                }
                {
                  cusList &&
                  <Paging page={props.list.page} total={props.list.total} fun={(page = 1, pageSize = 10): any => {
                    props.toggleCusPage(pageMsg)
                    setPageMsg({
                      query: pageMsg.query,
                      page: page,
                      pagesize: pageSize
                    })
                  }}/>
                }
                <Modal
                    title={popCusStyle}
                    visible={visible}
                    onCancel={handleCancel}
                    destroyOnClose
                    footer={null}
                >
                  <Form onFinish={handleOk}>
                    {
                      popCusStyle === '' &&
                      <Form.Item label={''} name="name" rules={[
                        {
                          required: true,
                          message: '',
                        },
                      ]}>
                        <Input autoFocus={true} placeholder=""/>
                      </Form.Item>
                    }
                    {
                      popCusStyle === '' &&
                      <Form.Item initialValue={editValue.name} label={''} name="name" rules={[
                        {
                          required: true,
                          message: '',
                        },
                      ]}>
                        <Input autoFocus={true} placeholder=""/>
                      </Form.Item>
                    }

                    {
                      popCusStyle === '' &&
                      <Form.Item label={''} name="telephone" rules={[
                        {
                          required: true,
                          message: '',
                        },
                      ]}>
                        <Input type={"number"} placeholder=""/>
                      </Form.Item>
                    }
                    {
                      popCusStyle === '' &&
                      <Form.Item initialValue={editValue.telephone} label={''} name="telephone" rules={[
                        {
                          required: true,
                          message: '',
                        },
                      ]}>
                        <Input type={"number"} placeholder=""/>
                      </Form.Item>
                    }
                    {
                      popCusStyle === '' &&
                      <Form.Item
                          name="level"
                          label=""
                          rules={[{ required: true, message: '' }]}
                      >
                        <Select placeholder="">
                          {
                            props.cateList &&
                            props.cateList.map((value: any) => (
                                <Option value={value._id}
                                        key={value._id}>
                                  {value.name}
                                </Option>
                            ))
                          }
                        </Select>
                      </Form.Item>
                    }
                    {
                      popCusStyle === '' &&
                      <Form.Item
                          initialValue={editValue.level}
                          name="level"
                          label=""
                          rules={[{ required: true, message: '' }]}
                      >
                        <Select placeholder="">
                          {
                            props.cateList &&
                            props.cateList.map((value: any) => (
                                <Option value={value._id}
                                        key={value._id}>
                                  {value.name}
                                </Option>
                            ))
                          }
                        </Select>
                      </Form.Item>
                    }
                    {
                      popCusStyle === '' &&
                      <Form.Item initialValue={1} label={''} style={{ marginLeft: 10 }} name="status">
                        <Select placeholder=''>
                          <Option value={1}></Option>
                          <Option value={0}></Option>
                          <Option value={-1}></Option>
                        </Select>
                      </Form.Item>
                    }
                    {
                      popCusStyle === '' &&
                      <Form.Item initialValue={editValue.status} label={''} style={{ marginLeft: 10 }} name="status">
                        <Select placeholder=''>
                          <Option value={1}></Option>
                          <Option value={0}></Option>
                          <Option value={-1}></Option>
                        </Select>
                      </Form.Item>
                    }
                    <Form.Item style={{ marginBottom: 4 }}>
                      <Space style={{ float: "right" }}>
                        <Button type="default" onClick={handleCancel}></Button>
                        {
                          popCusStyle === '' &&
                          <Button type="primary" htmlType="submit"></Button>
                        }
                        {
                          popCusStyle === '' &&
                          <Button type="primary" htmlType="submit"></Button>
                        }
                      </Space>
                    </Form.Item>
                  </Form>
                </Modal>
              </Card>
            </ConfigProvider>
          </DocumentTitle>
      );
    }
;

export default connect(mapStateToProps, mapDispatchToProps)(CusList);
